* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

p,
h2 {
  margin: 0;

}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.country-item {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 10px;
  
  border: 1px solid palegreen;
  border-radius: 20px;
padding: 10px;
  
}

.country-item:not(:last-child) {
  margin-bottom: 15px;
}

.country-name {
  margin-bottom: 15px;
}

